import React, { useEffect, useState } from 'react'
import { Navigate, RouterProvider, createBrowserRouter} from 'react-router-dom'
import Layout from './component/layout/Layout.jsx'
import SignIn from './component/signIn/SignIn.jsx'
import SignUp from './component/signUp/SignUp.jsx'
import { jwtDecode } from 'jwt-decode'
import ProtectedRouter from './protectedData.jsx'
import Users from './component/users/Users.jsx'
import UserDetelse from './component/userDetelse/UserDetelse.jsx'
import Form  from "./component/form/Form.jsx"
import AllUser from './component/allUser/AllUser.jsx'
import AddUser from './component/adduser/AddUser.jsx'
import Success from './component/success/Success.jsx'


export default function App() {
const [userRole ,setUserRloe] =useState(null)

function userRoles(role){
  setUserRloe(role)
}

  useEffect(()=>{

    userRoles()
    if (localStorage.getItem('token')){
      let token = localStorage.getItem("token")

      let Data = jwtDecode(token)
      saveUserData(Data)
    }

  },[])



// log out
function logout(){
  saveUserData(null)
  localStorage.removeItem("token")
  return  <Navigate to='/'/>
}



  let [userData,setUserData] = useState(null)  
  function saveUserData(data){
    setUserData(data)
  }





  let Routes = createBrowserRouter([
    {path:'/',element:<Layout userRole={userRole}  logout={logout} userData={userData}/>,children:[
      
      {path:'users',element:<ProtectedRouter><Users/></ProtectedRouter>},
      {path:'UserDetelse/:id',element:<ProtectedRouter><UserDetelse/></ProtectedRouter>},
      {path:'form',element:<ProtectedRouter><Form/></ProtectedRouter>},
      {path:'alluser',element:<ProtectedRouter><AllUser/></ProtectedRouter>},
      {path:'adduser',element:<ProtectedRouter><AddUser/></ProtectedRouter>},
      {path:'success',element:<ProtectedRouter><Success/></ProtectedRouter>},
      {path:'/',element:<SignIn userRoles={userRoles} userData={userData} saveUserData={saveUserData} />},
      {path:'signup',element:<SignUp/>},
      
  ]}  
  ])

  return<>



    <RouterProvider router={Routes}/>
  
  </>
}
