import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import $ from "jquery"
export default function Users() {

const baseurl = "https://epay-form-47um.onrender.com"

const [users,setUsers] = useState([])
    const headerData={
        token:localStorage.getItem("token"),
    }


    useEffect(()=>{
        $(".loading").fadeIn(1000)
        user()
        $(".loading").fadeOut(1000)
    },[])
    

    async function user(){
        $(".loading").fadeIn(1000)
        const {data} =await axios.get(`${baseurl}/api/v1/form`,  {
            headers: headerData,
            })
        
        setUsers(data.user)
        $(".loading").fadeOut(1000)
    }

    return <>

<div className="loading position-fixed top-0 bottom-0 end-0 start-0 opacity-50  ">
    

    <svg className="pl " width="240" height="240" viewBox="0 0 240 240">
    <circle class="pl__ring pl__ring--a" cx="120" cy="120" r="105" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 660" stroke-dashoffset="-330" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--b" cx="120" cy="120" r="35" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 220" stroke-dashoffset="-110" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--c" cx="85" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--d" cx="155" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
  </svg>
  
  
  </div>


<div className="container">
    <div className="row">
        <div className="col-md-12">
            <p className='fw-bold fs-2 my-5'><span className='text-danger'>All</span> Forms</p>
        </div>
        
            {users == null ? "" :<>
            
            {users.map((val)=>{
                return<>
               <div className='formes my-2'>

               <div className="col-md-12 my-3 ">
                    <p className='mx-3 me-auto fw-bold fs-4'>User Name:<span className='text-danger'>{val.firstName + val.lastName}</span></p>
                    <p className='mx-3 me-auto fw-bold fs-4'>Email :<span className='text-danger'> {val.email}</span></p>
                    <p className='mx-3 me-auto fw-bold fs-4'>government : <span className='text-danger'>{val.government}</span></p>
                </div>
                <div>
                <button className='btn btn-danger' ><Link className="nav-link  fw-bold text-uppercase "  aria-current="page" to={"/UserDetelse/"+val._id}><p>show User</p></Link></button>
                </div>

               </div>
                </>
            })}
            
            </>}
       
    </div>
</div>
  </>
}
