import axios from 'axios'
import React, { useEffect, useState } from 'react'
import $ from "jquery"

export default function AllUser() {

    const baseUrl = "https://epay-form-47um.onrender.com"
    const [allUsers , setAllUsers] = useState([])
    const [length , setlength] = useState(null)

useEffect(()=>{
   

    $(".loading").fadeIn(1000)
    allUser()
    
$(".loading").fadeOut(1000)


},[])

    async function allUser(){
        const {data} = await axios.get(`${baseUrl}/api/v1/user`)

        setAllUsers(data.user)
        setlength(data.length)
    }


    const headerData={
        token:localStorage.getItem("token"),
      }

    async function deletProduct(id){
        $(".loading").fadeIn(1000)
        let {data} = await axios.delete(`${baseUrl}/api/v1/user/${id}`,{
            headers:headerData
        })
        setAllUsers(data.data)
        setlength(data.data.length)
        console.log(data.data);
    $(".loading").fadeOut(1000)

    }



  return <>
  
  <div className="loading position-fixed top-0 bottom-0 end-0 start-0 opacity-50  ">
    

  <svg className="pl " width="240" height="240" viewBox="0 0 240 240">
	<circle class="pl__ring pl__ring--a" cx="120" cy="120" r="105" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 660" stroke-dashoffset="-330" stroke-linecap="round"></circle>
	<circle class="pl__ring pl__ring--b" cx="120" cy="120" r="35" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 220" stroke-dashoffset="-110" stroke-linecap="round"></circle>
	<circle class="pl__ring pl__ring--c" cx="85" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
	<circle class="pl__ring pl__ring--d" cx="155" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
</svg>


</div>

  <div className="container">
    <div className="row">
        <div className="col-md-12">
            <p className='fw-bold fs-2 my-5'><span className='text-danger'>All</span> Users</p>
            <p className='my-2 fw-bolder fs-3'>users lenth : <span className='text-danger'>{length}</span></p>
        </div>
            {allUsers == null ? "" :<>
            
           
                    
            {allUsers.map((val)=>{
                return<>
                <div className="col-md-4  text-center formes ">
                    
                    <p className='mx-3 me-auto fw-bold text-center my-3'>{val.name }</p>
                    <p className='mx-3 me-auto fw-bold text-center my-3'>{val.email}</p>
                    <p className='mx-3  fw-bold text-center my-3'>{val.role}</p>

                    <button onClick={()=>deletProduct(val._id)}  className='btn text-main border border-3 p-1 my-3'><i className="fa-regular fa-trash-can px-1 text-danger"></i>remove</button>
                </div>
                </>
            })}
              
            
            </>}
       
    </div>
</div>
  
  </>
}
