import axios from 'axios'
import { useFormik } from 'formik'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from "yup";
import $ from "jquery"

export default function AddUser() {

  useEffect(()=>{
   

    $(".loading").fadeIn(1000)
    setLoding()
    
$(".loading").fadeOut(1000)


},[])
    
  let [loding,setLoding]=useState(false)

  let [errorMessage,setErrorMessage]=useState("")

  const basurl = "https://epay-form-47um.onrender.com"
  let nav = useNavigate()



  let validationSchema = Yup.object({
    name: Yup.string().required("name Required").min(3, "min length 3").max(20, "max length 20"),
    email: Yup.string().email("enter valid email").required("Username Required").min(3, "min length 3"),
    password: Yup.string().required("Password Required").matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, "Password must contains at least one uppercase letter and at least one special character"),
    rePassword:Yup.string().required("confirm Password required").oneOf([Yup.ref("password")]),
  });

  let registeform = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      role:""
    },
    onSubmit,
    validationSchema,
  });

  const headerData={
    token:localStorage.getItem("token"),
  }


  async function onSubmit(valus){
    $(".loading").fadeIn(1000)
    setLoding(true)
    let {data}=await axios.post(`${basurl}/api/v1/user`,valus,{
        headers:headerData
    }).catch((error)=>{
        setErrorMessage(error.response.data.error)
        $(".loading").fadeOut(1000)
        
    setLoding(false)
    })
    setLoding(false)

    if(data.message == 'success'){
      nav('/success')
      $(".loading").fadeOut(1000)
    }


    
  }


  return (
    <>


  
<div className="loading position-fixed top-0 bottom-0 end-0 start-0 opacity-50  ">
    

    <svg className="pl " width="240" height="240" viewBox="0 0 240 240">
    <circle className="pl__ring pl__ring--a" cx="120" cy="120" r="105" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 660" stroke-dashoffset="-330" stroke-linecap="round"></circle>
    <circle className="pl__ring pl__ring--b" cx="120" cy="120" r="35" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 220" stroke-dashoffset="-110" stroke-linecap="round"></circle>
    <circle className="pl__ring pl__ring--c" cx="85" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
    <circle className="pl__ring pl__ring--d" cx="155" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
  </svg>
  
  
  </div>


      <div className="container py-5">
        <div className="row align-items-center">
                  
          <div className="col-md-12 py-5 px-5 ">
            <div className="px-5">
              <h2 className="fw-bold text-center">Add<span className=" text-danger">User</span></h2>    
              {errorMessage == "" ? "":<div className="alert alert-danger">{errorMessage}</div>}
              <form className='form-control' onSubmit={registeform.handleSubmit}>
                <div className="my-3">
                  <input  className="form-control form border-bottom border-1 border-dark custom-input"  type="text"  name="name"  id="name"  placeholder="Name"  onChange={registeform.handleChange}  onBlur={registeform.handleBlur} />
                  {registeform.touched.name ?<p className="text-danger">{registeform.errors.name}</p>:""}
                </div>

                <div className="my-3">
                  <input className="form-control  form border-bottom border-1 border-dark custom-input" type="email" name="email" id="email" placeholder="Email" onChange={registeform.handleChange} onBlur={registeform.handleBlur} />
                  {registeform.touched.email ?<p className="text-danger">{registeform.errors.email}</p> :""}

                </div>

                <div className="my-3">
                  <input className="form-control form border-bottom border-1 border-dark custom-input" type="password" name="password" id="password" placeholder="Password" onChange={registeform.handleChange} onBlur={registeform.handleBlur} />
                  {registeform.touched.password ?<p  className="text-danger">{registeform.errors.password}</p>:""}
                </div>

                <div className="my-3">
                  <input className="form-control form border-bottom border-1 border-dark custom-input" type="password" name="rePassword" id="rePassword" placeholder="ConfirmPassword" onChange={registeform.handleChange} onBlur={registeform.handleBlur} />
                  {registeform.touched.rePassword? <p className="text-danger">{registeform.errors.rePassword}</p>:""}
                </div>

                <div className="my-3">
  <p className="fw-bold">Role</p>
  <div>
    <input
      
      type="radio"
      name="role"
      id="user"
      value="user"
      onChange={registeform.handleChange}
      onBlur={registeform.handleBlur}
    />
    <label htmlFor="experienceNotExperienced">user</label>
  </div>
  <div>
    <input
      
      type="radio"
      name="role"
      id="admin"
      value="admin"
      onChange={registeform.handleChange}
      onBlur={registeform.handleBlur}
    />
    <label htmlFor="experienceLessThanOneYear">admin</label>
  </div>


</div>


                {loding ?<button type='button' className='btn btn-danger m-auto d-block w-50 my-3'><i className="fa-solid fa-spinner fa-spin"></i></button>:<button disabled={!(registeform.isValid && registeform.dirty)} type="submit"className="btn btn-danger  m-auto d-block w-100 my-3">Create User</button>}  


                 </form>
              
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
