import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import $ from "jquery"
export default function UserDetelse() {

const baseUrl ="https://epay-form-47um.onrender.com"

    let {id} = useParams()
    const [users,setUsers] = useState([])

    const headerData={
        token:localStorage.getItem("token"),
    }


    useEffect(()=>{
        $(".loading").fadeIn(1000)
        user()
        $(".loading").fadeOut(1000)
    },[])
    

    async function user(){
    
        const {data} =await axios.get(`${baseUrl}/api/v1/form/${id}`,  {
            headers: headerData,
            })

        
        setUsers(data.user)
        console.log(data.user);
        $(".loading").fadeOut(1000)
    }

// console.log(users);


  return <>

<div className="loading position-fixed top-0 bottom-0 end-0 start-0 opacity-50  ">
    

    <svg className="pl " width="240" height="240" viewBox="0 0 240 240">
    <circle class="pl__ring pl__ring--a" cx="120" cy="120" r="105" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 660" stroke-dashoffset="-330" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--b" cx="120" cy="120" r="35" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 220" stroke-dashoffset="-110" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--c" cx="85" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
    <circle class="pl__ring pl__ring--d" cx="155" cy="120" r="70" fill="none" stroke="#000" stroke-width="20" stroke-dasharray="0 440" stroke-linecap="round"></circle>
  </svg>
  
  
  </div>

  <div className="container">
    <div className="row">
        <div className="co">
            <h2 className='fw-bold my-5'>User Form</h2>
        </div>
        <div className="col-md-12  d-blodk col-lg-6 mt-3">
            {users ? <>
                <p className='fw-bold fs-5 py-3'>firstName : <span className='text-danger'> {users.firstName}</span> </p>
                <p className='fw-bold fs-5 py-3'>lastName : <span className='text-danger'> {users.lastName}</span> </p>
                <p className='fw-bold fs-5 py-3'>email : <span className='text-danger'> {users.email}</span> </p>
                <p className='fw-bold fs-5 py-3'>education : <span className='text-danger'> {users.education}</span> </p>
                <p className='fw-bold fs-5 py-3'>experience : <span className='text-danger'> {users.experience}</span> </p>
                <p className='fw-bold fs-5 py-3'>nationality : <span className='text-danger'> {users.nationality}</span> </p>
                <p className='fw-bold fs-5 py-3'>status : <span className='text-danger'> {users.status}</span> </p>
                <p className='fw-bold fs-5 py-3'>government : <span className='text-danger'> {users.government}</span> </p>
                <p className='fw-bold fs-5 py-3'>detailedAdress : <span className='text-danger'> {users.detailedAdress}</span> </p>
                <p className='fw-bold fs-5 py-3'>cv : <span className='text-danger'> <Link className='text-danger' to={users.cv}> {users.cv} </Link> </span> </p>
                <p className='fw-bold fs-5 py-3'>phone : <span className='text-danger'> {users.phone}</span> </p>
                <p className='fw-bold fs-5 py-3'>whatsApp : <span className='text-danger'> {users.whatsApp}</span> </p>
                <p className='fw-bold fs-5 py-3'>language : <span className='text-danger'>  {users.language}</span> </p>


                <p className='fw-bold fs-5 py-3'>1-What skills do you have for this job?
                : <span className='text-danger'> <br></br>  {users.skillsQ1}</span> </p>
                <p className='fw-bold fs-5 py-3'>2-If you had the opportunity to travel to work at the company's headquarters, would you accept it or not?
 : <span className='text-danger'> <br></br>  {users.travelQ2}</span> </p>
                <p className='fw-bold fs-5 py-3'>3- and explaining the reason in both cases?
 : <span className='text-danger'> <br></br>  {users.reasonQ3}</span> </p>
                <p className='fw-bold fs-5 py-3'>4-How do you make quick decisions?
 : <span className='text-danger'> <br></br>  {users.decisionsQ4}</span> </p>
                <p className='fw-bold fs-5 py-3'>
5-Why do you want to work in our company?
 : <span className='text-danger'> <br></br>  {users.ourCompanyQ5}</span> </p>
                <p className='fw-bold fs-5 py-3'>6-What are your weaknesses?
 : <span className='text-danger'> <br></br>  {users.weaknessesQ6}</span> </p>
                <p className='fw-bold fs-5 py-3'>7-Tell me about your dream job? : <span className='text-danger'> <br></br>  {users.dreamQ7}</span> </p>
                <p className='fw-bold fs-5 py-3'>8-How can you handle work pressure? : <span className='text-danger'> <br></br>  {users.pressureQ8}</span> </p>
                <p className='fw-bold fs-5 py-3'>9-What are your long-term career goals? : <span className='text-danger'> <br></br>  {users.goalsQ9}</span> </p>
                <p className='fw-bold fs-5 py-3'>10-How do you manage your time?
 : <span className='text-danger'> <br></br>  {users.manageQ10}</span> </p>
                <p className='fw-bold fs-5 py-3'>11-What are your strengths? : <span className='text-danger'> <br></br>  {users.strengthsQ11}</span> </p>
                <p className='fw-bold fs-5 py-3'>12-
What salary do you expect? : <span className='text-danger'> <br></br>  {users.expectQ12}</span> </p>
                <p className='fw-bold fs-5 py-3'>13-
How do handle constructive criticism?
 : <span className='text-danger'> <br></br>  {users.constructiveQ13}</span> </p>
                <p className='fw-bold fs-5 py-3'>14-Do you pay attention to details in your work? : <span className='text-danger'> <br></br>  {users.attentionQ14}</span> </p>
                <p className='fw-bold fs-5 py-3'>15-Can you work effectively as a part of a team? 
 : <span className='text-danger'> <br></br>  {users.effectivelyQ15}</span> </p>
                <p className='fw-bold fs-5 py-3'>16-Why shouldIhireyou? : <span className='text-danger'> <br></br>  {users.shouldIHireYouQ16}</span> </p>



            </>:""}
        </div>
    </div>
  </div>
  </>
}
